import React, { useState } from "react";

import { FaTimes, FaAngleLeft, FaAngleRight } from "react-icons/fa";
import Button from "../Button";

import styles from "./style.module.css";

type Props = {
  images: Array<string>;
  handleClose: () => void;
};

const Gallery: React.FC<Props> = ({ images, handleClose }) => {
  const [indexToShow, setIndexToShow] = useState(0);

  return (
    <div className={styles.gallery}>
      <div className={styles.closeButton}>
        <Button
          onClick={() => handleClose()}
          color="light"
          variant="transparent"
          size="l"
          icon
        >
          <FaTimes></FaTimes>
        </Button>
      </div>
      <div className={styles.prevButton}>
        {indexToShow > 0 && (
          <Button
            onClick={() => setIndexToShow(indexToShow - 1)}
            color="light"
            variant="transparent"
            size="l"
            icon
          >
            <FaAngleLeft></FaAngleLeft>
          </Button>
        )}
      </div>
      <a href={images[indexToShow]}>
        <img
          src={images[indexToShow]}
          alt={`Preview of ${indexToShow + 1}`}
        ></img>
      </a>
      <div className={styles.nextButton}>
        {indexToShow < images.length - 1 && (
          <Button
            onClick={() => setIndexToShow(indexToShow + 1)}
            color="light"
            variant="transparent"
            size="l"
            icon
          >
            <FaAngleRight></FaAngleRight>
          </Button>
        )}
      </div>
    </div>
  );
};

export default Gallery;
