import React, { useState } from "react";

import { CardItem } from "../../types";

import styles from "./style.module.css";
import Gallery from "../Gallery";

type Props = CardItem;

const Card: React.FC<Props> = ({ title, children, imagesURL, withImage }) => {
  const imagePlacehodler =
    "data:image/png;base64,";
  const [showGallery, setShowGallery] = useState(false);

  return (
    <div className={styles.card}>
      {withImage && (
        <button className={styles.image} onClick={() => setShowGallery(true)}>
          <img
            src={imagesURL[0] || imagePlacehodler}
            alt={title || "Card"}
          ></img>
        </button>
      )}
      <div className={styles.body}>
        {title && <div className={styles.header}>{title}</div>}
        <div className={styles.children}>{children}</div>
      </div>
      {imagesURL[0] && showGallery && (
        <Gallery
          images={imagesURL}
          handleClose={() => setShowGallery(false)}
        ></Gallery>
      )}
    </div>
  );
};

export default Card;
