import React from "react";
import cx from "classnames";
import styles from "./style.module.css";

type Props = {
  align: "left" | "right" | "center";
  pageSection?: boolean;
};

const Row: React.FC<Props> = ({ children, align, pageSection }) => {
  return (
    <div
      className={cx(styles.row, styles[align], {
        [styles.pageSection]: pageSection,
      })}
    >
      {children}
    </div>
  );
};

export default Row;
